	module.exports = {"data":{"page":{"id":"/Users/user/Repos/ayipv2/content/pages/2--press/index.md absPath of file >>> MarkdownRemark","html":"<h2>Bio</h2>\n<p>Angus Yip discontinued his computer science course to work as a software engineer in the Silicon Valley. He had been an active part of the developer community while participating and organizing tech events such as hackathons, spanning topics including UI/UX, AI, and blockchain.</p>\n<p>Recently, the economic growth in South East Asia and, of course, Yip’s roots, were good enough reasons for him to participate in the SEA startup scene, beginning in the Philippines region and the blockchain consulting sector.</p>\n<p>Yip’s story isn’t common in Asia when over-filtering applicants based on their credentials is standard practice. Knowing this, Yip envisioned an accessible job market, where the lack of credentials won’t be a handicap to applicants or block companies from acquiring capable talents.</p>\n<p>Using methodologies inspired by leading tech companies, Yip started Vagrant Limited, an HR firm that looks beyond a talent’s credentials to ensure companies the widest access to an incredible pool of problem-solving, critical thinking, and output-oriented talents.</p>\n<p>Leveraging his knowledge as a full-stack developer, expertise in ICOs, and experience as a startup entrepreneur, Yip actively engages in community events and conferences.</p>\n<h2>Alt Bio (Vagrant-focused)</h2>\n<p>Angus Yip founded the HR firm Vagrant Inc, so his own story of becoming a self-taught skilled labor can be more common in a world where a diploma and certifications matter too much.</p>\n<p>Using methodologies inspired by leading tech companies, Vagrant enables companies the widest access to problem-solving, critical thinking, and output-oriented talents. Fill managerial and technical openings with help from a team well-versed in HR, IT, and inter-personal skills.</p>\n<p>Let’s set sail on the sea of talents.</p>\n<h2>High Resolution Pictures</h2>\n<p><a href=\"https://photos.app.goo.gl/rC96xeBYhSRGuunt6\">Google Photos folder</a></p>","frontmatter":{"title":"Press Resources"}},"footnote":{"id":"/Users/user/Repos/ayipv2/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>Built by <a href=\"https://ayip.io\">Angus Yip</a> with ❤️</li>\n<li>All rights reserved 2018</li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/press/"}}

